.root {
  width: 100vw;
  max-width: 768px;
  min-height: 100vh;
  background-color: #fff;
  position: absolute;
  left: 0;
  top: 0;
  visibility: visible;
  opacity: 1;
  -webkit-animation: root 0.24s ease;
          animation: root 0.24s ease;
  z-index: 1000; }

.wrapper {
  min-height: 100vh;
  position: relative; }

@-webkit-keyframes root {
  0% {
    visibility: hidden;
    opacity: 0; }
  100% {
    visibility: visible;
    opacity: 1; } }

@keyframes root {
  0% {
    visibility: hidden;
    opacity: 0; }
  100% {
    visibility: visible;
    opacity: 1; } }

.closeBtn {
  position: absolute;
  right: 0;
  top: 0;
  color: #000 !important;
  background-color: #fff;
  border: 1px solid #e3e3e3; }

.links {
  padding-left: 24px;
  padding-right: 24px; }

.headerLinks {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  font-size: 18px;
  list-style: none;
  padding-top: 20%; }

.headerLink {
  font-size: 1em;
  font-weight: 600;
  display: block;
  color: #333;
  -webkit-transition: color 0.2s;
  -o-transition: color 0.2s;
  transition: color 0.2s;
  letter-spacing: 1px;
  padding-top: 25px;
  padding-bottom: 25px; }

.headerLink:hover {
    color: #fb383b; }

.footerLinks {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  padding-top: 12px;
  padding-bottom: 12px; }

.footerLink {
  color: #000;
  width: 50%;
  padding-top: 12px;
  padding-bottom: 12px; }

.copyRight {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  padding-top: 30px;
  padding-bottom: 30px; }

.copyRight ul {
    margin: 0;
    margin-bottom: 25px; }

.copyRight ul li img {
      width: 20px;
      height: 20px; }
